import React, { useEffect, useState, useReducer } from "react";
import useLocalStorage from "./useLocalStorage";
import Card from "./Card";
import "./style.css";

const menu = {};
  (function(e){
    e.bg = undefined;
    e.carousel = undefined;
    e.items = undefined;
    e.wrapper = undefined;
    e.idxs = undefined;

    e.menu = undefined;
    e.list = undefined;
    e.listItems = undefined;
    e.activeItem = undefined;
    e.ids = undefined;
    e.lstMimWidth = undefined;
    e.lstMaxWidth = undefined;
    e.listClosed = true;
    e.listTitle = [" ", ""];

    e.changeIdxs = function(idxs, dir, maxIdx){
      if(~dir){
        for(let i = 0; i < idxs.length; i++){
          if(idxs[i] === 0){
            idxs[i] = maxIdx;
          }
          else{
            idxs[i]--;
          }
        }
      }
      else{
        for(let i = 0; i < idxs.length; i++){
          if(idxs[i] === maxIdx){
            idxs[i] = 0;
          }
          else{
            idxs[i]++;
          }
        }
      }
      return idxs;
    }
    e.clearItems = function(idxs, items){
      if(!idxs.length || idxs.length < 3){
        return;
      }
      for(let i = 0; i < 3; i++){
        if(items.length > idxs[i] && ~idxs[i]){
          items[idxs[i]].classList.remove("item1");
          items[idxs[i]].classList.remove("item2");
          items[idxs[i]].classList.remove("item3");
        }
      }
    }
    e.addItems = function(idxs, items, wrapper){
      if(!idxs.length || idxs.length < 3){
        return;
      }
      (~idxs[0]) && items[idxs[0]].classList.add("item1");
      items[idxs[1]].classList.add("item2");
      if(e.bg && e.bg.length && e.bg[idxs[1]]){
        wrapper.style["background"] = e.bg[idxs[1]];
      }
      e.changeActiveItem(e.listItems[idxs[1]]);
      (~idxs[2]) && items[idxs[2]].classList.add("item3");
    }
    e.animate = function({duration, draw, timing, callback}) {
      let start = performance.now();
      requestAnimationFrame(function animate(time) {
        let timeFraction = (time - start) / duration;
        if (timeFraction > 1) timeFraction = 1;

        let progress = timing(timeFraction)

        draw(progress);

        if (timeFraction < 1) {
          requestAnimationFrame(animate);
        }
      });
      if(typeof(callback) === "function"){
        callback();
      }
    }

    e.changeActiveItem = function(newItem){
      if(newItem){
        if(e.activeItem){
          e.activeItem.classList.toggle("list-item-active");
        }
        e.activeItem = newItem;
        e.activeItem.classList.toggle("list-item-active");
      }
    }
    e.turnList = () =>{
      let arw = document.getElementsByClassName("arrow");
      for(let a of arw){
        a.classList.toggle("hidden");
      }
      e.animate({
        duration: 250,
        draw(progress) {
          if(e.listClosed){
            progress = 1 - progress;
          }
          e.list.style["width"] = e.lstMaxWidth - ((e.lstMaxWidth - e.lstMimWidth) * progress) + "px";
        },
        timing(timeFraction) {
          return (1 - timeFraction);
        },
        callback() {
          let t = document.querySelector(".list-title");
          t.innerHTML = e.listClosed ? e.listTitle[1] : e.listTitle[0];
          e.listClosed = !e.listClosed;
        }
      });
    }
    e.turnMenu = function() {
      e.menu.classList.toggle("hidden");
    }
    e.goToItem = (id) =>{
      e.clearItems(e.idxs, e.items);
      const offset = id - e.idxs[1];
      for(let i = 0; i < Math.abs(offset); i++){
        e.idxs = e.changeIdxs(Array.from(e.idxs), offset < 0 ? 1 : -1, e.items.length - 1);
      }
      e.addItems(e.idxs, e.items, e.wrapper);
    }
    e.turnAdd = function(createCard){
      document.querySelector(".add-container").classList.toggle("hidden");
      if(createCard){
        createCard.clearForm();
      }
    }
    e.update = (allCards) =>{
      e.bg = allCards.map((cardData) =>(cardData.background));
      e.carousel = document.querySelector(".carousel");
      e.items = document.getElementsByClassName("carousel__item");
      e.wrapper = document.querySelector(".wrapper");

      // while(e.items.length && e.items.length < 3){
      //   let first = e.items[0].cloneNode(true);
      //   e.carousel.appendChild(first);
      //   e.items = document.getElementsByClassName("carousel__item");
      // }
      e.idxs = [0, 1, 2];

      e.list = document.querySelector(".header__list");
      e.lstMimWidth = document.querySelector(".list-header").offsetWidth;
      e.lstMaxWidth = e.list.scrollWidth;
      e.list.style["width"] = e.lstMimWidth + "px";
      e.listItems = document.getElementsByClassName("list-item");
      e.changeActiveItem(e.listItems[Math.min(e.listItems.length - 1, e.idxs[1])]);
      e.clearItems(e.idxs, e.items);
      for(let i in e.idxs){
        if(e.items.length > e.idxs[i] && ~e.idxs[i]){
          e.items[e.idxs[i]].classList.add("item" + (parseInt(i,10) + 1));
        }
      }

      let n = [];
      for(let i = 0; i < e.listItems.length; i++){
        n.push(i);
      }
      e.ids = n;

      for(let i = 0; i < e.listItems.length; i++){
        e.listItems[i].param = e.ids[i];
      }
    }
    e.init = () =>{
      //e.bg = data.map((cardData) =>(cardData.background));// there is a data
      e.carousel = document.querySelector(".carousel");
      e.items = document.getElementsByClassName("carousel__item");
      //e.wrapper = document.querySelector(".wrapper");

      // while(e.items.length && e.items.length < 3){
      //   let first = e.items[0].cloneNode(true);
      //   e.carousel.appendChild(first);
      //   e.items = document.getElementsByClassName("carousel__item");
      // }

      e.idxs = [0, 1, 2];
      for(let i in e.idxs){
        if(e.items.length > e.idxs[i]){
          e.items[e.idxs[i]].classList.add("item" + (parseInt(i,10) + 1));
        }
      }
      //e.wrapper.style["background"] = e.bg[e.idxs[1]];
      e.carousel.addEventListener("click", (event) =>{
        let tmp = event.target.closest(".carousel__item");
        if(!tmp){
          return;
        }
        if(tmp.classList.contains("item1")){
          e.clearItems(e.idxs, e.items);
          e.idxs = e.changeIdxs(Array.from(e.idxs), 1, e.items.length - 1);//left
          e.addItems(e.idxs, e.items, e.wrapper);
        }
        if(tmp.classList.contains("item3")){
          e.clearItems(e.idxs, e.items);
          e.idxs = e.changeIdxs(Array.from(e.idxs), -1, e.items.length - 1);//right
          e.addItems(e.idxs, e.items, e.wrapper);
        }
      });

      e.menu = document.querySelector(".header__menu-cntnt");
      e.list = document.querySelector(".header__list");
      e.lstMimWidth = document.querySelector(".list-header").offsetWidth;
      e.lstMaxWidth = e.list.scrollWidth;
      e.list.style["width"] = e.lstMimWidth + "px";
      e.listItems = document.getElementsByClassName("list-item");
      // e.changeActiveItem(e.listItems[e.idxs[1]]);

      // let n = [];
      // for(let i = 0; i < e.listItems.length; i++){
      //   n.push(i);
      // }
      // e.ids = n;

      // for(let i = 0; i < e.listItems.length; i++){
      //   e.listItems[i].param = e.ids[i];
      // }

      document.addEventListener("click", (event) =>{
        if(event.target.closest(".list-header")){
          e.turnList();
          return;
        }
        if(event.target.classList.contains("list-item")){
          e.goToItem(event.target.param);
          e.changeActiveItem(event.target);
          return;
        }
        if(event.target.closest(".header__menu") || 
        event.target.closest(".menu-close")){
          e.turnMenu();
          return;
        }
        if(event.target.closest("#addCard") || 
        event.target.closest("#addAccept") ||
        event.target.closest("#addClose")){
          e.turnAdd();
        }
      });
      window.addEventListener("resize", () =>{
        e.list.style["width"] = "auto";
        e.lstMimWidth = document.querySelector(".list-header").offsetWidth;
        e.lstMaxWidth = e.list.scrollWidth;
        e.list.style["width"] = (e.listClosed ? e.lstMimWidth : e.lstMaxWidth) + "px";
      })
    }
  })(menu);

export default function Home({props}) {
  const shops = ["", " ", "", ""];
  const cardNumCheck = [
    (num)=>{
      if(num.length && num.length === 12){
        return num;
      }
      return "";
    },
    (num)=>{
      if(num.length && num.length === 17){
        return num;
      }
      return "";
    },
    (num)=>{
      if(num.length && num.length === 16){
        return num.match(/\d{4}/ig).join(" ");
      }
      return "";
    },
    (num)=>{
      if(num.length && num.length === 12){
        return num;
      }
      return "";
    }
  ]
  const cardTemplates = [
    {
      icon: "lenta_logo.png",
      title: "",
      number: "",
      id: -1,
      codeData: {
        data: "", 
        algorythm: "code128",
        type: "barcode"
      },
      background: "#244097"
    },
    {
      icon: "lerua_logo.png",
      title: " ",
      number: "",
      id: -1,
      codeData: {
        data: "", 
        algorythm: "code128",
        type: "barcode"
      },
      background: "#2ca52c"
    },
    {
      icon: "magnit_logo.png",
      title: "",
      number: "",
      id: -1,
      codeData: {
        data: "E",
        algorythm: "qrcode",
        type: "qrcode"
      },
      background: "#e20a17"
    },
    {
      icon: "5ka_logo.png",
      title: "",
      number: "",
      id: -1,
      codeData: {
        data: "", 
        algorythm: "ean13", 
        type: "barcode"
      },
      background: "linear-gradient(120deg, #6fa87194, #d9515185), linear-gradient(45deg, #e00000db, #246f27f2)"
    }
  ]
  const [loaded, setLoaded] = useState(!1);

  const initialNewCard = {
    title: "",
    number: "",
  }

  const newCardActions = {
    setName: "setcardName",
    setNumber: "setcardCode",
    clear: "resetState"
  }

  const [newCardData, dispatchNewCard] = useReducer((state, action) =>{
    switch(action.type) {
      case newCardActions.setName:
        return {...state, title: action.data};
      case newCardActions.setNumber:
        return {...state, number: action.data};
      case newCardActions.clear:
        return {title: "", number: ""};
      default:
        return state;
    }
  }, initialNewCard);

  const [newData, setNewData] = useLocalStorage("cards", []);
  const data = [
    {
      icon: "lenta_logo.png",
      title: "",
      number: "802512153323",
      id: 1,
      codeData: {
        data: "802512153323", 
        algorythm: "code128",
        type: "barcode"
      },
      background: "#244097"
    },
    {
      icon: "magnit_logo.png",
      title: "",
      number: "7000 0022 8480 4854",
      id: 2,
      codeData: {
        data: "E7000002284804854",
        algorythm: "qrcode",
        type: "qrcode"
      },
      background: "#e20a17"
    },
    {
      icon: "lerua_logo.png",
      title: " ",
      number: "93010091040684716",
      id: 3,
      codeData: {
        data: "93010091040684716", 
        algorythm: "code128",
        type: "barcode"
      },
      background: "#2ca52c"
    },
    {
      icon: "5ka_logo.png",
      title: "",
      number: "264378044733",
      id: 4,
      codeData: {
        data: "264378044733", 
        algorythm: "ean13", 
        type: "barcode"
      },
      background: "linear-gradient(120deg, #6fa87194, #d9515185), linear-gradient(45deg, #e00000db, #246f27f2)"
    }
  ];

  function newCard(){
    this.name = undefined;
    this.number = undefined;
  }
  newCard.prototype = {
    clearForm: function(){
      dispatchNewCard({type: newCardActions.clear, data: ""})
      document.getElementById("chooseShop").classList.remove("active");
    },
    turnList: function(prop){
      document.querySelector(".newcard__list-container").classList.toggle("hidden");
      if(typeof(prop) === "string"){
        dispatchNewCard({type: newCardActions.setName, data: prop});
        document.getElementById("chooseShop").classList.add("active");
      }
    },
    acceptNewCard: function(){
      if(shops.includes(newCardData.title)){
        let nd = cardTemplates[shops.indexOf(newCardData.title)];
        nd.number = cardNumCheck[shops.indexOf(newCardData.title)](newCardData.number);
        if(!nd.number){
          return;
        }
        nd.id = typeof(newData.length) === "number"?newData.length + 1:-1;
        nd.codeData.data += newCardData.number;
        setNewData(Array.from(newData).concat([nd]));
      }
    },
    init: function(){
      document.querySelector(".newcard__list-items").addEventListener("click", (event) =>{
        if(event.target.closest(".newcard__list-item")){
          if(event.target.closest(".newcard__list-item").hasAttribute("prop")){
            this.turnList(event.target.closest(".newcard__list-item").getAttribute("prop"));
          }
        }
      });
    }
  }
  const createCard = new newCard();



  function importAll(r) {
    let images = {};
    r.keys().forEach((item, index) => { images[item.replace('./', '')] = r(item); });
    return images
  }
  const icons = importAll(require.context('../Assets/icons', false, /\.(png|jpe?g|svg)$/));
  const logos = importAll(require.context('../Assets/logo', false, /\.(png|jpe?g|svg)$/));

  useEffect(() =>{
    //console.log(newData);
    if(!loaded){
      menu.init();
      createCard.init();
      setLoaded(!0);
    }
    if(loaded){
      menu.update(newData);
    }
  }, [loaded, newData]);
  const cardNameList = () =>{
    return(
      <div className="list-container">
          <div className="header__list">
            <div className="list-header">
              <div className="list-title"> </div>
              <img className="arrow" src={icons["arrow1.png"]} alt="open"/>
              <img className="arrow hidden" src={icons["arrow2.png"]} alt="close"/>
            </div>
            {
              newData.length > 0 && newData.map((cardData) =>(
                <div className="list-item" key={196 + "-" + cardData.title + "-" + cardData.number}>{cardData.title}</div>
              ))
            }
          </div>
        </div>
    )
  }
  const cards = () =>{
    let temp = [];
    for(let i = 3; i > newData.length; i--){
      temp.push({icon: "", title: "", codeData: "", id: i});
    }
    temp = temp.concat(Array.from(newData));
    console.log(temp);
    return(
      <div className="carousel">
        {
          // (newData.length > 0) && newData.map((cardData) =>(
          // <Card props={cardData} key={cardData.id}/>))
          (temp.length > 0) && temp.map((cardData) =>(
            <Card props={cardData} key={cardData.id}/>))
        }
      </div>
    )
  }
  const chooseCardList = () =>{
    return(
      <div className="newcard__list">
        <div className="newcard__list-items">
          {
            data.length > 0 && shops.map((shop) =>(
              <div className="newcard__list-item" key={197 + "-" + shop} prop={shop}>
                <img src={logos[cardTemplates[shops.indexOf(shop)].icon]} alt={shop}/>
                <div className="newcard__list-item-name">{shop}</div>
              </div>
            ))
          }
        </div>
      </div>
    )
  }

  return (
    <div className="wrapper">
      <div className="header">
        <div className="header__menu">
          <img src={icons["menu.png"]} alt="menu"/>
        </div>
        <div className="header__menu-cntnt hidden">
          <div className="header__menu-item"> </div>
          <div className="header__menu-item" id="addCard"> </div>
          <button className="header__menu-item" onClick={()=>{setNewData([]);}} style={{color: "red"}}>  </button>
          <div className="header__menu-item menu-close">
            <img src={icons["clear.png"]} alt="close"/>
          </div>
        </div>
        {
          cardNameList()
        }
      </div>
      {
        cards()
      }
      <div className="add-container hidden">
        <div className="add">
          <div className="add-title">  </div>
          <div className="add__item">
            <div className="add__item-title"></div>
            <div className="add__item-content">
              <input type="button" id="chooseShop" value={newCardData.title || " "} onClick={createCard.turnList}/>
            </div>
            <div className="newcard__list-container hidden">
              {
                chooseCardList()
              }
            </div>
          </div>
          <div className="add__item">
            <div className="add__item-title"> </div>
            <div className="add__item-content">
              <input type="text" id="newCardNum" value={newCardData.number || ""} 
              onChange={(event) => {dispatchNewCard({type: newCardActions.setNumber, data: event.target.value.match(/\d{0,}/i)[0]})}} placeholder=" "/>
            </div>
          </div>
          <div className="newcard__list-controls">
            <button className="list-control" id="addAccept"><img src={icons["accept.svg"]} alt="accept" 
            onClick={()=>{createCard.acceptNewCard(); createCard.clearForm()}}/></button>
            <div className="list-control" id="addClear"><img src={icons["cancel.svg"]} alt="clear" onClick={createCard.clearForm}/></div>
            <div className="list-control add-close" id="addClose" onClick={createCard.clearForm}></div>
          </div>
        </div>
      </div>
    </div>
  );
}



